/*
SCSS Build File


General Layout
*/
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300;400;600;900&display=swap");
html, body {
  height: 100%;
  min-height: 100%;
  background: #0f0d1d;
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
  position: relative;
  z-index: -1;
}

.sr-only {
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  display: block;
}

/*

Header Styles

*/
header {
  position: absolute;
  display: flex;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  height: 115px;
  box-sizing: border-box;
  top: 0;
  width: 100%;
}

.logo {
  width: 100%;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  margin: 0;
  text-align: center;
}
@media all and (min-width: 768px) {
  .logo {
    max-width: 315px;
    min-width: 315px;
  }
}
.logo img {
  width: 80%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

body.nav-open {
  overflow: hidden;
}

nav.primary {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
nav.primary ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: flex-start;
}
@media all and (min-width: 1280px) {
  nav.primary ul {
    flex-direction: row;
    position: static;
    width: auto;
  }
}
nav.primary ul.navigation {
  position: fixed;
  padding: 0;
  width: 100vw;
  height: calc(100vh - 150px);
  background: rgba(0, 0, 0, 0.9);
  left: 100%;
  top: 115px;
  z-index: 10;
  flex-direction: column;
  transition: 0.33s all ease-in-out;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation {
    position: static;
    width: 100%;
    height: auto;
    background: transparent;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}
.nav-open nav.primary ul.navigation {
  left: 0;
}
nav.primary ul.navigation li {
  padding: 0;
  line-height: 100px;
  height: 100px;
  margin: 0;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation li {
    line-height: 1rem;
    height: auto;
    margin-right: 40px;
  }
}
nav.primary ul.navigation a {
  padding: 0 40px;
  width: 100%;
  height: 100%;
  display: block;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation a {
    width: auto;
    height: auto;
    border: 0;
    padding: 0;
    display: inline-block;
  }
}
nav.primary ul.navigation a::after {
  position: absolute;
  height: 2px;
  background: #fff;
  width: 0;
  bottom: -5px;
  left: 0;
  border-radius: 2px;
  transition: all 0.33s ease-in-out;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation a::after {
    content: "";
  }
}
nav.primary ul.navigation a:hover::after {
  width: 100%;
}
nav.primary ul.navigation a.active::after {
  width: 15px;
}
nav.primary ul.navigation a.active:hover::after {
  width: 100%;
}
nav.primary ul.social-media {
  padding-right: 25px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  margin-left: auto;
}
nav.primary ul.social-media li {
  margin-right: 0;
}
nav.primary ul.social-media a {
  width: 26px;
  height: 100%;
  display: block;
  background: transparent no-repeat center;
  background-size: 14px;
}
nav.primary ul.social-media a.facebook {
  background-image: url("/assets/dist/images/facebook.png");
}
nav.primary ul.social-media a.youtube {
  background-image: url("/assets/dist/images/youtube-brands.svg");
}
nav.primary li {
  color: #fff;
  font-size: 18px;
  line-height: 115px;
  margin-right: 30px;
}
nav.primary a {
  color: #fff;
  text-decoration: none;
  position: relative;
}

/* mobile menu toggle button */
.menu-toggle {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 116px;
  padding: 40px 25px;
  cursor: pointer;
  box-sizing: border-box;
  order: 4;
}
@media all and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }
}
.menu-toggle span {
  margin: 0 auto;
  position: relative;
  top: 12px;
  transition-duration: 0s;
  transition-delay: 0.2s;
  transition: background-color 0.3s;
  width: 40px;
  height: 6px;
  background-color: #fff;
  display: block;
  opacity: 1;
}
.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  content: "";
  width: 40px;
  height: 6px;
  background-color: #fff;
  display: block;
  opacity: 1;
}
.menu-toggle span::before {
  margin-top: -12px;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0;
}
.menu-toggle span::after {
  margin-top: 12px;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0;
}
.nav-open .menu-toggle span {
  background-color: rgba(0, 0, 0, 0);
  transition: 0.3s background-color;
}
.nav-open .menu-toggle span::before {
  margin-top: 0;
  transform: rotate(45deg);
  transition-delay: 0, 0.2s;
}
.nav-open .menu-toggle span::after {
  margin-top: 0;
  transform: rotate(-45deg);
  transition-delay: 0, 0.2s;
}

.quick-contact {
  font-size: 14px;
  display: flex;
  justify-content: center;
  color: #e7e7e7;
  position: absolute;
  bottom: -50px;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  text-align: center;
  padding: 14px 0px;
}
@media all and (min-width: 1280px) {
  .quick-contact {
    width: 320px;
    flex-direction: column;
    position: static;
    padding: 0px 25px;
    border: 0;
    text-align: left;
  }
}
.quick-contact a {
  color: #fff;
  text-decoration: none;
  margin-left: 15px;
}
@media all and (min-width: 1280px) {
  .quick-contact a {
    margin-left: 0;
  }
}

.qc-text {
  color: #fff;
  opacity: 0.8;
}

/*

Banner Styles
*/
:root {
  --nav-height: 115px;
}

.page-banner {
  position: relative;
  z-index: -1;
  background: transparent;
  height: calc(100vh - var(--nav-height));
  padding-top: var(--nav-height);
  background: #0f0d1d url("/assets/dist/images/banner-background.png") no-repeat 100% top;
  background-size: cover;
}
.page-banner .page-banner-content {
  width: 100%;
  max-width: calc(100% - 120px);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100% - var(--nav-height));
}
@media all and (min-width: 720px) {
  .page-banner .page-banner-content {
    max-width: calc(100% - 480px);
  }
}
.page-banner h1 {
  font-family: "Jost", sans-serif;
  font-weight: 900;
  font-size: 90px;
  text-indent: 17px;
  letter-spacing: -0.002rem;
  text-transform: uppercase;
  color: #fff;
  width: 100%;
  text-align: left;
  margin: 0;
}
.page-banner .btn {
  margin-top: 40px;
  align-self: flex-start;
}

.mouse {
  background: #4e5559 linear-gradient(transparent 0%, transparent 50%, #ffffff 50%, #ffffff 100%);
  position: relative;
  width: 26px;
  height: 44px;
  border-radius: 100px;
  background-size: 100% 200%;
  opacity: 0.5;
  margin: 0 auto;
  transition: all 0.33s ease-out;
  animation: colorSlide 15s linear infinite, nudgeMouse 15s ease-out infinite;
}
.mouse:before, .mouse:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.mouse:before {
  width: 23px;
  height: 41px;
  background-color: #222a30;
  border-radius: 100px;
}
.mouse:after {
  background-color: #ffffff;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  animation: trackBallSlide 15s linear infinite;
}
.mouse:hover, .mouse:focus {
  opacity: 1;
  cursor: pointer;
}

@keyframes colorSlide {
  0% {
    background-position: 0% 100%;
  }
  20% {
    background-position: 0% 0%;
  }
  21% {
    background-color: #4e5559;
  }
  29.99% {
    background-color: #ffffff;
    background-position: 0% 0%;
  }
  30% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  50% {
    background-position: 0% 0%;
  }
  51% {
    background-color: #4e5559;
  }
  59% {
    background-color: #ffffff;
    background-position: 0% 0%;
  }
  60% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  80% {
    background-position: 0% 0%;
  }
  81% {
    background-color: #4e5559;
  }
  90%, 100% {
    background-color: #ffffff;
  }
}
@keyframes trackBallSlide {
  0% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  6% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  14% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  15%, 19% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  28%, 29.99% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  30% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  36% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  44% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  45%, 49% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  58%, 59.99% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  60% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  66% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  74% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  75%, 79% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  88%, 100% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
}
@keyframes nudgeMouse {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(8px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(8px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(8px);
  }
  90% {
    transform: translateY(0);
  }
}
@keyframes nudgeText {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(2px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(2px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(2px);
  }
  90% {
    transform: translateY(0);
  }
}
@keyframes colorText {
  21% {
    color: #4e5559;
  }
  30% {
    color: #ffffff;
  }
  51% {
    color: #4e5559;
  }
  60% {
    color: #ffffff;
  }
  81% {
    color: #4e5559;
  }
  90% {
    color: #ffffff;
  }
}
